* {
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

body {
  padding: 60px;
}

input {
  width: 100%;
  color: gray;
  border: 1px solid black;
  padding: 10px 5px;
}

.card-grid {
  display: flex;
  flex-flow: row wrap;
  margin: 0 auto;
}

.card {
  align-content: center;
  border: 1px solid green;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  width: 200px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  width: 100%;
  height: 150px;
  object-fit: contain;
}